import React, { Component } from 'react'
import { DropdownButton, Dropdown } from 'react-bootstrap'
import Connect from '../../Reuse/Connect'
import RadioBtn from '../../Reuse/RadioBtn'
import '../Home.css'

class DepWith extends Component {
  constructor(props) {
    super(props)
    this.state = {
      // token: props.token,
      // tokenAmt: props.tokenAmt,
    }
    this.handleAmt = this.handleAmt.bind(this)
    this.handleTok = this.handleTok.bind(this)
  }
  handleAmt(e) {
    // console.log('e', e)
    this.props.amtSelected(e)
  }

  handleTok(e) {
    let { innerText } = e.target
    this.props.tokSelected(innerText)
  }

  render() {
    const { token, tokenAmt } = this.props

    const DropDownCoin = () => (
      <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between', placeItems: 'center' }}>
        <div style={{ margin: '0 .5vw 0 0' }}>{token}</div>
        <div className={'ddArrow'}></div>
      </div>
    )

    const DepositWithdraw = (props) => (
      <div style={{ alignSelf: 'center', fontSize: '18px', fontWeight: 'bold', padding: '2px 4px' }}>
        {props.txt}
      </div>
    )

    return (
      <div style={{ border: '1px solid #d7dadc', borderRadius: '2px', height: '40vh', width: '30vw' }} className={'deposits'}>
        <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between', borderBottom: '1px solid #d7dadc' }}>
          <DepositWithdraw txt={'Deposit'} />
          <DepositWithdraw txt={'Withdraw'} />
        </div>
        <div style={{ padding: '0 4px' }}>Token</div>
        <DropdownButton 
          id={'dd-btn'}
          title={
            <DropDownCoin />
          }>
          <div className={'tokDropDown'}>
            <Dropdown.Item onClick={this.handleTok} className={'tokName'}>
              ADA
            </Dropdown.Item>
            <Dropdown.Divider />
            <Dropdown.Item onClick={this.handleTok} className={'tokName'}>
              DAI
            </Dropdown.Item>
            <Dropdown.Divider />
            <Dropdown.Item onClick={this.handleTok} className={'tokName'}>
              cDAI
            </Dropdown.Item>
            <Dropdown.Divider />
            <Dropdown.Item onClick={this.handleTok} className={'tokName'}>
              USDC
            </Dropdown.Item>
            <Dropdown.Divider />
            <Dropdown.Item onClick={this.handleTok} className={'tokName'}>
              USDT
            </Dropdown.Item>
            <Dropdown.Divider />
            <Dropdown.Item onClick={this.handleTok} className={'tokName'}>
              WBTC
            </Dropdown.Item>
          </div>
        </DropdownButton>

        <div style={{ padding: '0 4px' }}>Amount</div>

        <div style={{ display: 'flex', justifyContent: 'space-evenly', border: '1px solid orange' }}>
          <RadioBtn
            amt={'0.1'}
            selected={tokenAmt === '0.1'}
            token={token}
            tokenAmt={tokenAmt}
            onClick={this.props.amtSelected} />
          <RadioBtn
            amt={'1'}
            selected={tokenAmt === '1'}
            token={token}
            tokenAmt={tokenAmt}
            onClick={this.props.amtSelected} />
          <RadioBtn
            amt={'10'}
            selected={tokenAmt === '10'}
            token={token}
            tokenAmt={tokenAmt}
            onClick={this.props.amtSelected} />
          <RadioBtn
            amt={'100'}
            selected={tokenAmt === '100'}
            token={token}
            tokenAmt={tokenAmt}
            onClick={this.props.amtSelected} />
        </div>
        <Connect to={'/account'} />
      </div>
    )
  }
}

export default DepWith