import React, { Component } from 'react'
import { nanoid } from 'nanoid'
import './Home.css'

class Stats extends Component {
  constructor(props) {
    super(props)
    this.state = {
      usrIP: props.usrIP,
      arrTxn: [
        { txnNo: '12345', txnTime: '5 hours ago' },
        { txnNo: '12346', txnTime: '5 hours ago' },
        { txnNo: '12347', txnTime: '5 hours ago' },
        { txnNo: '12348', txnTime: '5 hours ago' },
        { txnNo: '12349', txnTime: '6 hours ago' },
        { txnNo: '12350', txnTime: '6 hours ago' },
        { txnNo: '12351', txnTime: '6 hours ago' },
        { txnNo: '12352', txnTime: '7 hours ago' },
        { txnNo: '12353', txnTime: '7 hours ago' },
        { txnNo: '12354', txnTime: '7 hours ago' },
        { txnNo: '12355', txnTime: '8 hours ago' },
      ],
    }
  }
  render() {
    const { usrIP, arrTxn } = this.state
    const { token, tokenAmt, uCount } = this.props
    return (
      <div style={{ border: '1px solid #d7dadc', borderRadius: '2px', height: '40vh', width: '30vw' }} className={'statistics'}>
        <div style={{ fontSize: '18px', fontWeight: 'bold', }}>Statistics {tokenAmt} {token}</div>
        <div>Anonymity set</div>
        <div>{uCount} equal user deposits</div>
        <div>latest deposits</div>
        <div style={{ display: 'flex', flexDirection: 'column', flexWrap: 'wrap', border: '1px solid lawngreen', height: '150px', margin: '5px 0 0 0' }}>
          {arrTxn.map((x, i) => (
            <div style={{ textAlign: 'center', backgroundColor: `${i % 2 === 1 ? '#1a1a1a' : 'transparent'}` }} key={nanoid(4)}>
              {x.txnNo}. {x.txnTime}
            </div>
          ))}
        </div>
        <div style={{ display: 'flex', justifyContent: 'space-between' }}>
          <div>Your IP</div>
          <div>{usrIP}</div>
        </div>
      </div>
    )
  }
}

export default Stats